.button {
  background: rgba(255, 255, 255, 0.25);
  border: solid 1px rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.25);
  cursor: pointer;
  color: white;
  text-transform: uppercase;
  padding: 10px 20px;
  width: 200px;
  margin: 4px 0;
}

.button:disabled {
  background: rgba(255, 255, 255, 0.1);
  border: solid 1px rgba(152, 152, 152, 1);
}
